@font-face {
  font-family: flamanteSeca;
  src: url(../fonts/Flamante-Seca-FFP.ttf);
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

div.Container {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 90vh;
  padding: 5vh;
  font-family: flamanteSeca;
  color: #1a1a1a;
}

.box {
  
}

.oct-box-title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-preferred-size: 10%;
      flex-basis: 10%;
  min-height: 10vh;
  font-size: 1.5em;
}

.oct-title {
  text-align: center;
  width: 100%;
}

.oct-box-columns {
  -ms-flex-preferred-size: 90%;
      flex-basis: 90%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.oct-box-columns > .box {
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-align: center;
      align-items: center;
}

.oct-social-float-buttons {
  position: absolute;
  bottom: 8vh;
  right: 8vh;
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.float-button {
  text-align: center;
}

.float-button > i {
  -webkit-transition: .1s linear;
  -o-transition: .1s linear;
  transition: .1s linear;
  -webkit-box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.8);
          box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.8);
  background-color: #3043B1;
  color: #FAFAFA;
  border-radius: 100%;
  padding: 1vh;
  margin: 0 1vh;
  font-size: 3vh;
  min-width: 3vh;
}

.float-button > i:hover {
  -webkit-box-shadow: 0px 5px 3px rgba(0, 0, 0, 0.8);
          box-shadow: 0px 5px 3px rgba(0, 0, 0, 0.8);
  -webkit-transform: translatey(-2px);
      -ms-transform: translatey(-2px);
          transform: translatey(-2px);
}

.float-button > i:active {
  -webkit-box-shadow: 0 0 0 #000;
          box-shadow: 0 0 0 #000;
  -webkit-transform: translatey(3px);
      -ms-transform: translatey(3px);
          transform: translatey(3px);
}


.oct--main-body--column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: 0.3s linear, color 0.2s linear;
  -o-transition: 0.3s linear, color 0.2s linear;
  transition: 0.3s linear, color 0.2s linear;
}

.oct--main-body--column--hovered{
  background-color: #DD6B00;
  color: #fafafa;
}

.oct--main-body--column-title {
  -webkit-transition: 0.2s linear;
  -o-transition: 0.2s linear;
  transition: 0.2s linear;
  text-align: center;
  font-size: 4.5vw;
  padding: 0 2vw;
}


@media (min-width: 769px) {
  .oct--main-body--column:not(:first-of-type) {
    border-left: 2px solid #ddd;
  }
  
  
  .oct--main-body--column-title--hovered {
    -webkit-transform: translateY(-5vh);
        -ms-transform: translateY(-5vh);
            transform: translateY(-5vh);
  }
}


@media (max-width: 769px) {
  div.Container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 0;
    font-family: flamanteSeca;
    color: #1a1a1a;
  }
  
  .oct-box-columns {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  
  
  .oct--main-body--column-title {
    -webkit-transition: 0.2s linear;
    -o-transition: 0.2s linear;
    transition: 0.2s linear;
    text-align: center;
    font-size: 5vh;
    padding: 0 2vw;
  }
  
  .oct-social-float-buttons {
    position: fixed;
    bottom: 4vh;
    right: 4vh;
    -ms-flex-direction: row;
        flex-direction: row;
    display: -ms-inline-flexbox;
    display: inline-flex;
  }
}


